<template>
	<view class="main">
		<view class="hand">
			<image src="../../static/tabBarimg/dbleft.png" @click="blackbtn"></image>
			费率绑定
		</view>
		<view class="show">
			<view class="sone">
				<view class="sonefoot">
					<view class="sfshang">
						{{list.projectName}}
					</view>
					<view class="sfxia">
						<view>创建时间：{{list.createTime}}</view>
						<view>项目地址：{{list.address}}</view>
					</view>
				</view>
			</view>
			<!-- <view class="stwo">
				<view class="stwohand">
					充电区名称
				</view>
				<input placeholder="请输入充电区的名称" v-model="name"/>
			</view> -->
			<view class="sthree">
				<view class="sthreehand">
					充电模式
				</view>
				<view class="sthreefoot">
					<view class="smont">
						<view class="sfleft">
							<view>常用</view>
							<van-switch v-model="type0" @change="cebtn(0)" />
						</view>
						<view class="sfright" v-show="type0==true">
							<span @click="xgbtn(onetap,0)">修改费率</span>
							<span @click="cebtn(0)" style="margin-left: 30upx;">更换套餐</span>
						</view>
					</view>
					<view class="smontwo" v-show="type0==true&&JSON.stringify(onetap)!='{}'">
						<view class="smleft">
							<view class="smshang">
								{{onetap.packageName}}
							</view>
							<view v-show="onetap.types=='B2'">
								<view class="smxia" v-for="(item1, index1) in onetap.steps" :key="index1">
									{{item1.standard}}元/度
								</view>
							</view>
							<view v-show="onetap.types=='B5'">
								<view class="smxia" v-for="(item1, index1) in onetap.steps" :key="index1">
									{{item1.standard}}元/度
								</view>
							</view>
							<view v-show="onetap.types=='B1'">
								<view class="smxia" v-for="(item1, index1) in onetap.steps" :key="index1"
									v-show="item1.minPower!=item1.maxPower">
									{{item1.minPower}}-{{item1.maxPower}}瓦-{{item1.standard}}元/时</view>
								<view class="smxia" v-for="(item1, index1) in onetap.steps" :key="index1"
									v-show="item1.minPower==item1.maxPower">{{item1.minPower}}瓦以上-{{item1.standard}}元/时
								</view>
							</view>
							<view v-show="onetap.types=='B3'">
								<view class="smxia" v-for="(item1, index1) in onetap.steps" :key="index1"
									v-show="item1.minPower!=item1.maxPower">
									{{item1.minPower}}-{{item1.maxPower}}瓦-{{item1.standard}}元/次</view>
								<view class="smxia" v-for="(item1, index1) in onetap.steps" :key="index1"
									v-show="item1.minPower==item1.maxPower">{{item1.minPower}}瓦以上-{{item1.standard}}元/次
								</view>
							</view>
							<view v-show="onetap.types=='B4'">
								<view class="smxia" v-for="(item1, index1) in onetap.steps" :key="index1"
									v-show="item1.minPower!=item1.maxPower">
									{{item1.minPower}}-{{item1.maxPower}}瓦-1元/{{item1.standard}}小时</view>
								<view class="smxia" v-for="(item1, index1) in onetap.steps" :key="index1"
									v-show="item1.minPower==item1.maxPower">{{item1.minPower}}瓦以上-1元/{{item1.standard}}小时
								</view>
							</view>
						</view>
						<view class="smright bscolor" v-show="onetap.types=='B1'">计时</view>
						<view class="smright blcolor" v-show="onetap.types=='B2'">计量</view>
						<view class="smright bccolor" v-show="onetap.types=='B3'">计次</view>
						<view class="smright bccolor" style="color:#ffaa00" v-show="onetap.types=='B4'">一元</view>
						<view class="smright bccolor" style="color:red" v-show="onetap.types=='B5'">计量2</view>
					</view>
				</view>

				<view class="sthreefoot" v-show="addNew==0">
					<view class="smont">
						<view class="sfleft">
							<view>包月</view>
							<van-switch v-model="type1" @change="cebtn(1)" />
						</view>
						<view class="sfright" v-show="type1==true">
							<span @click="xgbtn(twotap,1)">修改费率</span>
							<span @click="cebtn(1)" style="margin-left: 30upx;">更换套餐</span>
						</view>
					</view>
					<view class="smontwo" v-show="type1==true&&JSON.stringify(twotap)!='{}'">
						<view class="smleft">
							<view class="smshang">
								{{twotap.packageName}}
							</view>
							<view class="smxia" v-show="twotap.types=='A1'">
								{{twotap.monthStandard}}元/{{twotap.monthNum}}小时/月，加油包:
								{{twotap.comeStandard}}元{{twotap.comeNum}}小时
							</view>
							<view class="smxia" v-show="twotap.types=='A3'">
								{{twotap.monthStandard}}元/{{twotap.monthNum}}次/月，加油包:
								{{twotap.comeStandard}}元{{twotap.comeNum}}次
							</view>
							<view v-show="twotap.types!='A1'&&twotap.types!='A3'">
								<view class="smxia" v-for="(item1, index1) in twotap.steps" :key="index1"
									v-show="item1.minPower!=item1.maxPower">
									{{item1.minPower}}-{{item1.maxPower}}瓦-{{item1.standard}}元</view>
								<view class="smxia" v-for="(item1, index1) in twotap.steps" :key="index1"
									v-show="item1.minPower==item1.maxPower">{{item1.minPower}}瓦以上-{{item1.standard}}元
								</view>
							</view>
						</view>
						<view class="smright bscolor" v-show="twotap.types=='A1'">包时</view>
						<view class="smright bccolor" v-show="twotap.types=='A3'">包次</view>
						<view class="smright" style="color:#40D851;" v-show="twotap.types=='A4'">包总时</view>
						<view class="smright" style="color:#BC12ED;" v-show="twotap.types=='A5'">按天包时</view>
						<view class="smright" style="color:#0103FD;" v-show="twotap.types=='A6'">包总次</view>
						<view class="smright" style="color:#E40C00;" v-show="twotap.types=='A7'">按天包次</view>
						
					</view>
				</view>


				<view class="sthreefoot">
					<view class="smont">
						<view class="sfleft">
							<view>临时</view>
							<van-switch v-model="type2" @change="cebtn(2)" />
						</view>
						<view class="sfright" v-show="type2==true">
							<span @click="xgbtn(threetap,2)">修改费率</span>
							<span @click="cebtn(2)" style="margin-left: 30upx;">更换套餐</span>
						</view>
					</view>

					<view class="smontwo" v-show="type2==true&&JSON.stringify(threetap)!='{}'">
						<view class="smleft">
							<view class="smshang">
								{{threetap.packageName}}
							</view>
							<view class="smxia" v-for="(item1, index1) in threetap.steps" :key="index1"
								v-show="item1.minPower!=item1.maxPower">
								{{item1.minPower}}-{{item1.maxPower}}瓦-{{item1.standard}}元/时</view>
							<view class="smxia" v-for="(item1, index1) in threetap.steps" :key="index1"
								v-show="item1.minPower==item1.maxPower">{{item1.minPower}}瓦以上-{{item1.standard}}元/时
							</view>
						</view>
						<view class="smright bscolor" v-show="threetap.types=='C1'">计时</view>
					</view>

				</view>
			</view>
			<view class="sfour" @click="addcd">
				立即绑定
			</view>
			<van-popup v-model="wshow" :close-on-click-overlay="false">
				<view class="tannei1">
					<view class="tnhand">
						<view class="tntxt">请选择一个费率</view>
						<image src="../../static/tabBarimg/close.png" @click="wshowclose"></image>
					</view>
					<view class="tnfoot">
						<view class="montlist">
							<van-radio-group v-model="radio1" @change='changebtn'>
								<view class="sone1" v-show="alist.length>0||blist.length>0||clist.length>0">
									<input v-model="flname" />
									<view class="sonefoot" @click="sbtn">
										<image src="../../static/tabBarimg/search.png"></image>
									</view>
								</view>
								<view class="mont" v-for="(item, index) in alist" :key="index"
									v-show="active==0&&alist.length>0">
									<view class="montleft">
										<view v-show="item.types=='B2'">
											<view class="montshang">{{item.packageName}}</view>
											<view class="montxia">{{item.steps[0].standard}}元/度</view>
										</view>
										<view v-show="item.types=='B5'">
											<view class="montshang">{{item.packageName}}</view>
											<view class="montxia">{{item.steps[0].standard}}元/度</view>
										</view>
										<view v-show="item.types=='B1'">
											<view class="montshang">{{item.packageName}}</view>
											<view class="montxia" v-for="(item1, index1) in item.steps"
												:key="index1" v-show="item1.minPower!=item1.maxPower">
												{{item1.minPower}}-{{item1.maxPower}}瓦-{{item1.standard}}元/时</view>
											<view class="montxia" v-for="(item1, index1) in item.steps"
												:key="index1" v-show="item1.minPower==item1.maxPower">
												{{item1.minPower}}瓦以上-{{item1.standard}}元/时</view>
										</view>
										<view v-show="item.types=='B3'">
											<view class="montshang">{{item.packageName}}</view>
											<view class="montxia" v-for="(item1, index1) in item.steps"
												:key="index1" v-show="item1.minPower!=item1.maxPower">
												{{item1.minPower}}-{{item1.maxPower}}瓦-{{item1.standard}}元/次</view>
											<view class="montxia" v-for="(item1, index1) in item.steps"
												:key="index1" v-show="item1.minPower==item1.maxPower">
												{{item1.minPower}}瓦以上-{{item1.standard}}元/次</view>
										</view>
										<view v-show="item.types=='B4'">
											<view class="montshang">{{item.packageName}}</view>
											<view class="montxia" v-for="(item1, index1) in item.steps"
												:key="index1" v-show="item1.minPower!=item1.maxPower">
												{{item1.minPower}}-{{item1.maxPower}}瓦-1元/{{item1.standard}}小时</view>
											<view class="montxia" v-for="(item1, index1) in item.steps"
												:key="index1" v-show="item1.minPower==item1.maxPower">
												{{item1.minPower}}瓦以上-1元/{{item1.standard}}小时</view>
										</view>
									</view>
									<view class="montright">
										<view class="mrtxt bscolor" v-show="item.types=='B1'">计时</view>
										<view class="mrtxt blcolor" v-show="item.types=='B2'">计量</view>
										<view class="mrtxt bccolor" v-show="item.types=='B3'">计次</view>
										<view class="mrtxt bccolor" style="color:#ffaa00" v-show="item.types=='B4'">一元
										</view>
										<view class="mrtxt bccolor" style="color:red" v-show="item.types=='B5'">计量2
										</view>
										<van-radio :name="item.packageId" />
									</view>
								</view>
								<view v-show="active==0&&alist.length==0" style="margin: 120upx 50upx; color: #007AFF;"
									@click="gopz">
									您目前还未配置费率，点此马上配置；
								</view>
								<view class="mont" v-for="(item, index) in clist" :key="index"
									v-show="active==2&&clist.length>0">
									<view class="montleft">
										<view class="montshang">{{item.packageName}}</view>
										<view class="montxia" v-for="(item1, index1) in item.steps" :key="index1"
											v-show="item1.minPower!=item1.maxPower">
											{{item1.minPower}}-{{item1.maxPower}}瓦-{{item1.standard}}元/时</view>
										<view class="montxia" v-for="(item1, index1) in item.steps" :key="index1"
											v-show="item1.minPower==item1.maxPower">
											{{item1.minPower}}瓦以上-{{item1.standard}}元/时</view>
									</view>
									<view class="montright">
										<view class="mrtxt bscolor" v-show="item.types=='C1'">计时</view>
										<van-radio :name="item.packageId" />
									</view>
								</view>
								<view v-show="active==2&&clist.length==0" style="margin: 120upx 50upx; color: #007AFF;"
									@click="gopz">
									您目前还未配置费率，点此马上配置；
								</view>
								<view class="mont" v-for="(item, index) in blist" :key="index"
									v-show="active==1&&blist.length>0">
									<view class="montleft">

										<view class="montshang">{{item.packageName}}</view>
										<view class="montxia" v-show="item.types=='A1'">
											{{item.monthStandard}}元/{{item.monthNum}}小时/月，加油包:
											{{item.comeStandard}}元{{item.comeNum}}小时
										</view>
										<view class="montxia" v-show="item.types=='A3'">
											{{item.monthStandard}}元/{{item.monthNum}}次/月，加油包:
											{{item.comeStandard}}元{{item.comeNum}}次
										</view>
										<view v-show="item.types!='A1'&&item.types!='A3'">
											<view class="montxia" v-for="(item1, index1) in item.steps"
												:key="index1" v-show="item1.minPower!=item1.maxPower">
												{{item1.minPower}}-{{item1.maxPower}}瓦-{{item1.standard}}元</view>
											<view class="montxia" v-for="(item1, index1) in item.steps"
												:key="index1" v-show="item1.minPower==item1.maxPower">
												{{item1.minPower}}瓦以上-{{item1.standard}}元</view>
										</view>

									</view>
									<view class="montright">
										<view class="mrtxt bscolor" v-show="item.types=='A1'">包时</view>
										<view class="mrtxt bccolor" v-show="item.types=='A3'">包次</view>
										<view class="mrtxt" style="color:#40D851;" v-show="item.types=='A4'">包总时</view>
										<view class="mrtxt" style="color:#BC12ED;" v-show="item.types=='A5'">按天包时</view>
										<view class="mrtxt" style="color:#0103FD;" v-show="item.types=='A6'">包总次</view>
										<view class="mrtxt" style="color:#E40C00;" v-show="item.types=='A7'">按天包次</view>
										<van-radio :name="item.packageId" />
									</view>
								</view>
								<view v-show="active==1&&blist.length==0" style="margin: 120upx 50upx; color: #007AFF;"
									@click="gopz">
									您目前还未配置费率，点此马上配置；
								</view>
							</van-radio-group>
						</view>
					</view>
				</view>
			</van-popup>

		</view>
	</view>
</template>

<script>
	import Toast from '../../wxcomponents/vant/toast/toast';
	import Dialog from '../../wxcomponents/vant/dialog/dialog';
	export default {
		components: {},
		mounted() {

		},

		data() {
			return {
				showlist: [{
					name: '常用',
					type: false
				}, {
					name: '包月',
					type: false
				}, {
					name: '临时',
					type: false
				}],
				// checked: true,
				wshow: false,
				active: 0,
				areaId: '',
				radio1: '',
				projectId: '',
				list: {}, //小区详情
				alist: [], //常用费率列表
				blist: [], //包月费率列表
				clist: [], //临时费率列表
				onetap: {}, //常用费率数据
				twotap: {}, //包月费率数据
				threetap: {}, //临时费率数据
				commonId: '', //常用费率套餐id
				monthId: '', //包月费率套餐id
				tmpId: '', //临时费率套餐id
				type0: false,
				type1: false,
				type2: false,
				name: '', //充电区名
				flname: '', //费率名称
				addNew:0,
				areaType:0,
				isProportion:'',//运营商权限

			}
		},
		onLoad(options) {
			this.projectId = options.projectId
			this.areaId = options.areaId
			this.addNew = options.addNew
			this.areaType=options.areaType
			this.isProportion=options.isProportion
		},
		onShow() {
			this.main()
			this.showone()
			this.wshowclose()
		},
		methods: {
			//获取某一小区详情和总用户数等信息
			main() {
				let data = {
					projectId: this.projectId,
					needProjectMoney: 0,
					needProportion: 0
				}
				this.$base.request1('manager/project/statistics/', 'GET', data)
					.then(res => {
						this.list = res.data.data
					})
					.catch(err => {

					})
			},
			//获取一个充电区绑定的费率信息
			showone() {
				this.$base.request1('manager/area/pid' + '/' + this.areaId, 'GET')
					.then(res => {
						if (res.data.code == '200') {
							if (res.data.data.oftenList[0]) {
								this.$base.request1('manager/rate' + '/' + res.data.data.oftenList[0], 'GET')
									.then(res1 => {
										this.onetap = res1.data.data[0]
										this.commonId = res.data.data.oftenList[0]
										this.type0 = true
									})
									.catch(err => {

									})
							}
							if (res.data.data.monthlyList[0]) {
								this.$base.request1('manager/rate' + '/' + res.data.data.monthlyList[0], 'GET')
									.then(res1 => {
										this.twotap = res1.data.data[0]
										this.monthId = res.data.data.monthlyList[0]
										this.type1 = true
									})
									.catch(err => {

									})
							}
							if (res.data.data.temporaryList[0]) {
								this.$base.request1('manager/rate' + '/' + res.data.data.temporaryList[0], 'GET')
									.then(res1 => {
										this.threetap = res1.data.data[0]
										this.tmpId = res.data.data.temporaryList[0]
										this.type2 = true
									})
									.catch(err => {

									})
							}
						} else {
							this.$toast.fail(res.data.msg)
						}
					})
					.catch(err => {

					})
			},
			//返回上一页
			blackbtn() {
				uni.navigateBack()
			},
			//跳转配置费率页面
			gopz() {
				if(this.isProportion=='1'){
					uni.navigateTo({
						url: '../rate_setting/rate_setting?projectId=' + this.projectId,
					});
				}else{
					this.$toast.fail("您不是该小区的运营商，没有权限配置。")
				}
			},
			cebtn(vul) {
				this.active = vul
				if (vul == 0) {
					if (this.type0 == true) {
						this.wshowopen();
					}
				} else if (vul == 1) {
					Dialog.confirm({
						title: '提示',
						message: '请尽量不要修改包月费率，否则可能会引起包月用户无法使用'
					}).then(() => {
						if (this.type1 == true) {
							this.wshowopen();
						}
					}).catch(() => {
						// on cancel
					});

				} else if (vul == 2) {
					if (this.type2 == true) {
						this.wshowopen();
					}
				}

			},
			//关闭弹窗
			wshowclose() {
				this.flname = ''
				if (JSON.stringify(this.onetap) == '{}') {
					this.type0 = false
				}
				if (JSON.stringify(this.twotap) == '{}') {
					this.type1 = false
				}
				if (JSON.stringify(this.threetap) == '{}') {
					this.type2 = false
				}
				this.wshow = false
			},
			//查询费率
			sbtn() {
				this.cxfl()
			},
			//费率列表
			cxfl() {
				uni.showLoading({
					title: '加载中',
				})
				let data = {
					packageName: this.flname,
					type: this.active
				}
				this.$base.request1('manager/rate/list' + '/' + this.projectId, 'GET', data)
					.then(res => {
						uni.hideLoading()
						if (this.active == 0) {
							this.alist = res.data.data.rows
						} else if (this.active == 1) {
							this.blist = res.data.data.rows
						} else if (this.active == 2) {
							this.clist = res.data.data.rows
						}
					})
					.catch(err => {

					})
			},
			//打开弹窗
			wshowopen() {
				this.wshow = true
				this.cxfl()
			},
			//修改费率
			xgbtn(item, vul) {
				if (vul == 0) {
					uni.navigateTo({
						url: '../common_modification_rate/common_modification_rate?packageId=' + item.packageId +
							'&type=' + item.types+'&occNum='+this.areaType,
					});
				} else if (vul == 1) {
					if(item.types=='A1'||item.types=='A3'){
						uni.navigateTo({
							url: '../monthly_revised_rate/monthly_revised_rate?packageId=' + item.packageId +
								'&type=' + item.types+'&occNum='+this.areaType,
						});
					}else{
						uni.navigateTo({
							url: '../new_monthly_revised_rate/new_monthly_revised_rate?packageId=' + item.packageId +
								'&type=' + item.types+'&occNum='+this.areaType,
						});
					}
					
				} else if (vul == 2) {
					uni.navigateTo({
						url: '../temporary_modification_rates/temporary_modification_rates?packageId=' + item
							.packageId + '&type=' + item.types+'&occNum='+this.areaType,
					});
				}
			},
			changebtn() {

				this.$base.request1('manager/rate' + '/' + this.radio1, 'GET')
					.then(res => {
						if (this.active == 0) {
							this.onetap = res.data.data[0]
							this.commonId = this.radio1
						} else if (this.active == 1) {
							this.twotap = res.data.data[0]
							this.monthId = this.radio1
						} else if (this.active == 2) {
							this.threetap = res.data.data[0]
							this.tmpId = this.radio1
						}
						this.wshow = false
					})
					.catch(err => {

					})
			},
			//添加充电区
			addcd() {
				uni.showLoading({
					title: '请稍候',
					mask: true
				})
				if (this.type0 == false) {
					this.commonId = ''
					this.onetap = {}
				}
				if (this.type1 == false) {
					this.monthId = ''
					this.twotap = {}
				}
				if (this.type2 == false) {
					this.tmpId = ''
					this.threetap = {}
				}
				let data = {
					commonId: this.commonId,
					monthId: this.monthId,
					tmpId: this.tmpId,
				}

				this.$base.request1('manager/area/rate' + '/' + this.areaId, 'PUT', data)
					.then(res => {
						if (res.data.code == '200') {
							uni.hideLoading()
							this.$toast.success('修改成功')
							uni.navigateBack()
						} else {
							uni.hideLoading()
							this.$toast.fail(res.data.msg)
						}
					})
					.catch(err => {

					})
			}
		}
	}
</script>

